@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.descrWrapper {
  font-family: Montserrat;
  display: flex;
  flex-direction: column;
  padding-right:  20px;
  padding-bottom: 30px;
  width: 50%;
}

.descr-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #ff6060;
  color: white;
  border-radius: 7px;
  padding: 10px;
}

.descr-content {
  font-weight: 300;
  line-height: 1.6;
  display: none;
  color: #ff6060;
  background-color: #f6f6f6;
  border-radius: 7px;
  padding: 15px;
}

.chevron-icon-down {
  display: flex;
  color: rgb(255, 255, 255);
}


.rotateIcon{
  transform: rotateX(180deg);
}

/* Media querrie */

/* Mobile version */

@media screen and (max-width: 767px) {

  .descrWrapper {
    display: flex;
    flex-direction: column;
    padding-right: 20px;
    padding-bottom: 20px;
    width: 100%;
    font-size: 16px;
    
}
  
}