@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.aboutWrapper {

  padding: 0 150px;
}
.about-infoWrapper{
  width: 80%;
}

.about{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.aboutContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 280px;
  /* width: 100%; */
  background-color: black;
  border-radius: 25px;
  position: relative;
  margin-top: 50px;
}

.aboutImg {
  opacity: 0.8;
  height: 280px;
  width: 100%;
  object-fit: cover;
  background-color: #f7f7f7;
  border-radius: 25px;
  position: relative;
}

.aboutWrapperlinks {
  width: 100%;
  padding: 0 100px;
}

.aboutFiabilite {
  display: flex;
  align-items: center;
  color: white;
  width: 90%;
  background-color: #ff6060;
  margin: 20px;
  border-radius: 5px;
}



.about-infoWrapper {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.about-info-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #ff6060;
  color: white;
  border-radius: 7px;
  padding: 10px;
}

.about-info-content {
  font-weight: 300;
  line-height: 1.6;
  display: none;
  color: #ff6060;
  background-color: #f6f6f6;
  border-radius: 7px;
  padding: 15px;
}

.chevron-icon-down {
  display: flex;
  color: rgb(255, 255, 255);
}

.rotateIcon {
  transform: rotateX(180deg);
}


/* Media querrie */

/* Mobile version */

@media screen and (max-width: 767px) {

  .aboutWrapper {
    font-family: Montserrat;
    padding: 0 25px;
  }

  .aboutContainer {
    margin-top: 30px;
    height: 110px;
  }



.about-infoWrapper{
  width: 100%;
}

  .aboutImg {
    height: 110px;
  }
}

/* Tablet version */
/* tablet, landscape iPad, lo-res laptops ands desktops */

@media screen and (min-width: 768px) and (max-width: 1150px) {

  .aboutWrapper {
    padding: 0 25px;
    margin-top: 2px;
  }
}
